.wp-block-gutenberg-custom-gutenberg-block {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

.three-box-block {
    display: flex;
    flex-flow: row;
    gap: 20px;
}
.box-number-title {
    display: flex;
    flex-flow: row;
    gap: 10px;
}
.box-number-title .box-number {
    font-size: 18px;
    font-family: inherit;
    color: orange;
}
.box-number-title .box-title {
    font-size: 30px;
    font-weight: 500;
    color: #000;
    font-family: inherit;
}
.block h1.box-heading {
    font-size: 30px;
    font-weight: 800;
    line-height: 30px;
    font-family: inherit;
}
.block p.box-content {
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    font-family: inherit;
}
.three-box-block .block {
    border: 1px solid;
    padding: 10px;
    display: flex;
    flex-flow: column;
    align-items: center;
    text-align: center;
    border-radius: 15px;
	transition: box-shadow 0.3s ease;
}
.block:hover {
	box-shadow: 10px 10px 20px rgba(36, 36, 36, 0.5);
	cursor: pointer;
}

.gutenberg-subscription-block {
    border: 1px solid #000;
    padding: 40px 0;
    margin-top: 50px;
}
.gutenberg-block-wrapp {
    display: flex;
}
.gutenberg-block-image {
    background-image: url(http://localhost/wordpress/wp-content/uploads/2024/12/sale-and-special-offer-discount-shopping-vector-11647611.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 250px;
    min-height: 250px;
}
.gutenberg-block-content {
    margin: 0 auto;
    max-width: 350px;
    line-height: 1.5;
    padding-left: 40px;
    padding-right: 40px;

    h3 {
        font-size: 40px;
        margin: 0;
        line-height: 1.1;
    }
    form {
        border: 1px solid #000;
        display: flex;
        margin-top: 40px;
        margin-bottom: 0;
    }
    input {
        padding: 14px 20px;
        width: 100%;
        border: 0;
        outline: 0;
    }
    
    button {
        background-color: #000;
        color: #fff;
        padding: 8px 20px;
        border: 0;
        cursor: pointer;
        width: 50%;
    }
}